.Typeahead{position:relative}.List{position:absolute;z-index:1;top:100%;left:0;right:0;margin-top:3px;background:#fff;-webkit-box-shadow:0 5px 9px rgba(65,65,70,.14);box-shadow:0 5px 9px rgba(65,65,70,.14);border-radius:2px;border:1px solid #d2d2d7;opacity:0;pointer-events:none;-webkit-transform:scale(.96);-ms-transform:scale(.96);transform:scale(.96);-webkit-transition:.06s ease-out;-o-transition:.06s ease-out;transition:.06s ease-out}.open{opacity:1;pointer-events:auto;-webkit-transform:scale(1);-ms-transform:scale(1);transform:scale(1)}


